@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
  .link {
    @apply cursor-pointer font-semibold text-sm text-white hover:underline;
  }

  .server-default {
    @apply h-12 bg-[#36393f] rounded-full flex justify-center items-center cursor-pointer hover:rounded-2xl transition-all duration-100 ease-out;
  }

  .icon {
    @apply h-6 text-[#b9bbbe] cursor-pointer hover:text-[#dcddde];
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: revert;
}

main {
  background-color: #20333f;
}

header {
  background-color: #253a47;
}

.bg-salon {
  background-color: #212d39;
}

.bg-h {
  background-color: #212d39;
}

.bg-b {
  background-color: #1c252e;
}

.bg-all-chat {
  background: rgb(9, 9, 121);
  background: linear-gradient(180deg, #20333f 94%, #3b3059 100%);
}

.input {
  border-radius: 2rem;
}

.bg-hover-message:hover {
  background: linear-gradient(to right, #6747625e, #42275a9c);
  border-radius: 0.5rem;
}
.bg-hover-message {
  margin-left: 0.5rem;
}



.admin-page {
  padding: 20px;
  background-image: url(https://c4.wallpaperflare.com/wallpaper/728/715/790/blurred-minimalism-abstract-purple-wallpaper-preview.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

.user-list, .user-details {
  border: 1px solid #ccc;
  padding: 10px;
  margin-bottom: 20px;
}

.user-list ul {
  list-style-type: none;
  padding: 0;
}

.user-list li {
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: 5px 0;
}

.user-list img {
  border-radius: 50%;
  margin-right: 10px;
  width: 40px;
  height: 40px;
}

.user-details {
  background-color: #f9f9f9;
}

.user-details ul {
  list-style-type: none;
  padding: 0;
}

.user-details li {
  margin-bottom: 10px;
}
